import "core-js/modules/es.array.push.js";
import bottomNav from "@/components/bottomNav.vue";
import router from '@/router';
import { onMounted, reactive, ref, toRefs } from 'vue';
export default {
  components: {
    bottomNav
  },
  setup() {
    let boos = ref(false);
    let data = reactive({
      list: null,
      much: 0
    });
    let libraries = () => {
      router.push({
        path: "/libraries"
      });
    };
    let bianji = () => {
      boos.value = !boos.value;
    };

    //点击删除
    let del = key => {
      console.log(data.list.findIndex(item => item.key == key));
      let aa = data.list.findIndex(item => item.key == key);
      if (aa != -1) {
        data.list.splice(aa, 1);
        window.localStorage.setItem("losg", JSON.stringify(data.list));
      }
      data.much = data.list.length;
    };
    let govideo = item => {
      window.localStorage.setItem("datas", JSON.stringify(item));
      router.push({
        path: "/video"
      });
    };

    //获取数据
    onMounted(() => {
      data.list = JSON.parse(window.localStorage.getItem("losg"));
      console.log(data.list);
      if (data.list) {
        data.much = data.list.length;
      }
    });
    return {
      libraries,
      ...toRefs(data),
      boos,
      bianji,
      del,
      govideo
    };
  }
};